import { DatePipe, Location, NgStyle } from '@angular/common';
import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { AudioplayerService } from 'src/services/audioplayer.service';
import { CommonService } from 'src/services/common.service';
import { EventProviderService } from 'src/services/event-provider.service';
import { LoaderService } from 'src/services/loader.service';
import { ScheduleService } from 'src/services/schedule.service';
import { ToastService } from 'src/services/toast.service';
import { AppService } from '../app.service';

@Component({
  selector: 'app-schedule',
  templateUrl: './schedule.page.html',
  styleUrls: ['./schedule.page.scss'],
})
export class SchedulePage implements OnInit {
  week_days : any = ['Sun' , 'Mon' , 'Tue' , 'Wed' , 'Thu' , 'Fri' , 'Sat'];
  showsArray : any = []
  date_pipe = new DatePipe('en-US');
  current_date : any = new Date();
  today_date : any =  Number(this.date_pipe.transform(new Date(), 'dd'))
  current_month : any = (Number(this.date_pipe.transform(new Date() , 'MM') ))
  current_year : any = (Number(this.date_pipe.transform(new Date() , 'yyyy') ))
  current_hour : any = Number(this.date_pipe.transform((new Date) , 'H' ))
  current_minutes : any = Number(this.date_pipe.transform((new Date) , 'mm' ))
  current_day = new Date().getDay() // 0(Sunday) to 6(Saturday).
  weekly_Calendar : any = [];
  current_show : any = [];
  active_index:any;
  daysInMonth = new Date(new Date().getFullYear(), new Date().getMonth(), 0).getDate();
  
  today_date_index : any;
  index : any;
  prev_index : any;
  ch_id :any;
  constructor(private location : Location, private translate:TranslateService,
    private toastr:ToastService, private schedule:ScheduleService,private route: ActivatedRoute,
    public audioService:AudioplayerService,private appService : AppService,private event_provider : EventProviderService,private loader:LoaderService,
    public common:CommonService) { }



  ngOnInit() {

  }


  ionViewWillEnter() {
    this.ngOnInit();
  
    this.event_provider.getChurchData.subscribe(async (res: any) => {
        if (!(Object.keys(res).length === 0)) {
            // asking for password
            let app_pages = res.apppages.find(x => x.name === 'Schedule');
            if (app_pages.password_protection_toggle === 'true') {
                this.common.openPagePasswordModal(app_pages.page_protected_password, app_pages.selected_private_group);
            }
        }
    });
  
  }
  ionViewDidEnter() {
    // this.event_provider.hidefooter(false, 'other');
    this.ch_id = this.route.snapshot.paramMap.get('church_id');
    this.setCalendar()
  }
  

  gotobackPage() {
    this.location.back();
}

  //Function for setting weekly calendar
  setCalendar () {
    this.weekly_Calendar = [];
      for(let i = this.current_day, j = 0 ; i >= 0 ; i--,j++ ) {         //setting previous dates from current date
        this.weekly_Calendar.push({date : this.today_date - i , day : this.week_days[j] ,
           today : (this.today_date - i === this.today_date ? true : false) , month : this.current_month });
      }
      for(let i = this.current_day , j = 1 , k = this.current_day + 1 ; i < 6 ; i++, j++ , k++ ) { // setting next dates from current date
        this.weekly_Calendar.push({date : this.today_date + j , day : this.week_days[k] , 
           today : (this.today_date + j === this.today_date ? true : false) , month : this.current_month}); 
      }
          this.weekly_Calendar.forEach((e , i) => { //setting previous month dates
              if(e.date <= 0) {
                console.log(e , i)
                this.weekly_Calendar[i].date = e.date + this.daysInMonth;
                this.weekly_Calendar[i].month = e.month - 1;
              }
          })
          console.log(this.weekly_Calendar)
          this.weekly_Calendar.map(val => {
              this.translate.get(val.day).subscribe( (res: any) => {
                  val.day = res;
              });
        
          })
          
      this.today_date_index = this.weekly_Calendar.findIndex(element => {    //fetching index of current Date
        if (element.today === true) {
          return true;
        }
     })
     this.getList(this.today_date,this.current_month)    //Calling function for fetching current date shows
  }


//Function for getting list of shows by date
  getList(date ,month, i?) {
    this.loader.presentLoading().then(() => {
      this.current_show = [];
      this.showsArray = [];
      if(i >= 0){                  //if index are coming in function setting current date active color
        console.log("aya")
        this.weekly_Calendar[this.today_date_index].today = false;
        if(this.prev_index || this.prev_index === 0) {
          this.weekly_Calendar[this.prev_index].today = false
        }
      // this.prev_index ? this.weekly_Calendar[this.prev_index].today = false : '';
      this.weekly_Calendar[i].today ? this.weekly_Calendar[i].today  = false : this.weekly_Calendar[i].today = true;
      this.prev_index = i;
      }
       var body = {                         //Setting payload for fetching shows
        church_id : this.ch_id,
        date : this.date_pipe.transform(new Date() , 'yyyy') + '-' + month + '-' + date
       }

       this.schedule.getShowByDate(body).subscribe((res :any) => {   //Api call for fetching shows
          if(res.status) {                          //if status is true
            this.loader.stopLoading();
            this.showsArray = res.shows      //giving shows to array
            this.showsArray.forEach((e , i) => {            //Fetching active shows from shows list
              if(this.today_date === date) {               //checking if today date and user selected date is same or not
                if(this.date_pipe.transform(new Date() , 'HH:mm') >= this.date_pipe.transform(e.start_time , 'HH:mm')
                &&  this.date_pipe.transform(new Date() , 'HH:mm') <= this.date_pipe.transform(e.end_time , 'HH:mm')) {
                 this.current_show.push({index : i , active : true});  //pushing with true which shows are active in current show array
                 } else {
                  this.current_show.push({index : i , active : false}); //pushing with false which shows are not active in current show array
                }
              } else {
                this.current_show.push({index : i , active : false}); //pushing with false which shows are not active in current show array
              }
            })
        } else {           //if status is false
          this.loader.stopLoading();
        }                 
      },(err:any) => {
        this.toastr.presentToastWithOptions('check Your internet connection' , 'Sorry' , 'danger');
      })
    })
  
  }



//Function for Playing selected Show 
playShow() {

  }

}
